/* App.css */


.container {
  display: flex;
  justify-content: space-between;
  max-width: 1000px;
  margin: 10px auto;
  margin-top: 10px;
}

.editor-container {
  flex: 1;
  background-color: #f9f9f9;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  position: absolute;
  left: 0px;
  width: 450px;
}

.editor-title {
  text-align: start;
  color: #333;
  font-size: 20px;
  margin-bottom: 30px;
}

.editor-sections {
  margin-top: 20px;
}

.add-section-buttons button {
  margin-right: 10px;
  padding: 10px;
  background-color: #4caf50;
  color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.section-container {
  margin-top: 10px;
}

