.preview-container {
  position: absolute;
  right: 0px;
  background-color: #ffffff; /* Default background color */
  border: 1px solid #ddd;
  padding: 20px;
  width: 520px;
  height: 1200px;
  margin: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-left: 200px solid #000000;
  padding-left: 10px;
}


.preview-title {
  color: #ffffff;
  position: absolute;
  top: 20px;
  left: 0; /* Adjust left positioning */
  background-color: #333;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: bold;
  margin-bottom: 10px;
  width: 90%;
  padding: 10px;
  z-index: 1;
  margin-top: 20px;
  height: auto;
  margin-bottom: 0px;
}

.preview-title p {
  font-size: 12px;
  word-wrap: break-word;
  max-width: 50ch;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: bold;
  margin-top: 30px;
  margin-bottom: 0px;
  /* Add any other styles that suit your layout */
}

.preview-title h1 {
  font-size: 22px;
  word-wrap: break-word;
  max-width: 50ch;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: bold;
  margin-top: 90px;
  /* Add any other styles that suit your layout */
}


.preview-content {
  color: #777373;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: bold;
  width: 100%; /* Or set a specific width */
  height: auto; 
}

.preview-heading {
