/* PersonalInfoForm.css */

/* Container styles */

  
  /* Heading styles */
  h2 {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
  }
  
  /* Label styles */
  label {
    display: block;
    margin-bottom: 18px;
    font-weight: bold;
    color: #333;
  }
  
  /* Input styles */
  input {
    width: 80%;
    padding: 1px;
    margin-bottom: 15px;
    border: 4px solid #020202;
    border-radius: 4px;
    box-sizing: border-box;
    font-family: 'Open Sans';
   
    font-size: 20px;
  }
  
  /* Image Preview styles */
  h3 {
    font-size: 18px;
    margin-top: 20px;
    color: #333;
  }
  
  img {
    width: 100px;
    height: 100px;
    object-fit: cover;
    margin-top: 10px;
  }
  